import {
    takeLatest,
    put,
    all,
    call
} from 'redux-saga/effects';

import { USER_ACTION_TYPES } from './user.types';

import {
    signInSuccess,
    signInFailed,
    signUpSuccess,
    signUpFailed,
    signOutSuccess,
    signOutFailed
} from './user.action';

import { signInUser, signInUserWithGoogle, signUpUser, signOutUser } from '../../../utils/api/user.utils';
import { getCompaniesStart } from '../company/company.action';

export function* signInWithGoogle() {
    try {
        const response = yield call(signInUserWithGoogle);
        if (!response.success) {
            throw new Error(response.message);
        }
        yield put(signInSuccess(response.user));
    } catch (error) {
        yield put(signInFailed(error));
    }
}

export function* signInWithEmail({ payload: { email, password } }) {
    try {
        const response = yield call(signInUser, email, password);
        if (!response.success) {
            throw new Error(response.message);
        }
        yield put(signInSuccess(response.user));
    } catch (error) {
        yield put(signInFailed(error));
    }
}

export function* signUp({ payload: { email, password } }) {
    try {
        const response = yield call(signUpUser, email, password);
        if (!response.success) {
            throw new Error(response.message);
        }
        yield put(signUpSuccess(response.user));
    } catch (error) {
        yield put(signUpFailed(error));
    }
}

export function* signOut() {
    try {
        yield call(signOutUser);
        yield put(signOutSuccess());
    } catch (error) {
        yield put(signOutFailed(error));
    }
}

export function* startFetchCompanies() {
    yield put(getCompaniesStart());
}

export function* onGoogleSignInStart() {
    yield takeLatest(USER_ACTION_TYPES.GOOGLE_SIGN_IN_START, signInWithGoogle);
}

export function* onEmailSignInStart() {
    yield takeLatest(USER_ACTION_TYPES.EMAIL_SIGN_IN_START, signInWithEmail);
}

export function* onSignUpStart() {
    yield takeLatest(USER_ACTION_TYPES.SIGN_UP_START, signUp);
}

export function* onSignOutStart() {
    yield takeLatest(USER_ACTION_TYPES.SIGN_OUT_START, signOut);
}

export function* onSignInSuccess() {
    yield takeLatest(USER_ACTION_TYPES.SIGN_IN_SUCCESS, startFetchCompanies)
}

export function* userSagas() {
    yield all([
        call(onGoogleSignInStart),
        call(onEmailSignInStart),
        call(onSignUpStart),
        call(onSignOutStart),
        call(onSignInSuccess)
    ])
}