import axios from "axios";

import { API_URL } from './constants';

export const httpGetAllCompaniesForUser = async () => {
    try {
        const { data } = await axios.get(`${API_URL}/company`);
        return data;
    } catch (error) {
        console.error(error);
    }
}

export const httpAddCompanyByFirstDocument = async (companyName, documentType, summary, documentUrl) => {
    try {
        const { data } = await axios.post(`${API_URL}/company`, {
            companyName,
            documentType,
            summary,
            documentUrl
        }, {
            headers: {
                'Content-Type': 'application/json'
            }
        });
        return data;
    } catch (error) {
        console.error(error);
    }
}

export const httpUpdateCompanyName = async (companyId, newName) => {
    try {
        const { data } = await axios.patch(`${API_URL}/company/${companyId}`, {
            newName
        }, {
            headers: {
                'Content-Type': 'application/json'
            }
        })
        return data;
    } catch (error) {
        console.error(error);
    }
}

export const httpDeleteCompany = async (companyId) => {
    try {
        const { data } = await axios.delete(`${API_URL}/company/${companyId}`);
        return data;
    } catch (error) {
        console.error(error);
    }
}
