import axios from "axios";

import { API_URL } from "./constants";

export const httpAddDocumentForCompany = async (companyId, documentType, summary, documentUrl) => {
    try {
        const { data } = await axios.post(`${API_URL}/document/${companyId}`, {
            documentType,
            summary,
            documentUrl
        }, {
            headers: {
                'Content-Type': 'application/json'
            }
        });      
        return data;
    } catch (error) {
        console.error(error);
    }
}

export const httpDeleteDocument = async (documentId) => {
    try {
        const { data } = await axios.delete(`${API_URL}/document/${documentId}`);
        return data;
    } catch (error) {
        console.error(error);
    }
}