import axios from "axios";

import { API_URL } from "./constants";

export const httpGetQuestionsAndAnswersForDocument = async (documentId) => {
    try {
        const { data } = await axios.get(`${API_URL}/question/${documentId}`);
        return data;
    } catch (error) {
        console.error(error);
    }
}

export const httpAskQuestion = async (documentId, question) => {
    try {
        const { data } = await axios.post(`${API_URL}/question/${documentId}`, {
            question
        }, {
            headers: {
                'Content-Type': 'application/json'
            }
        });
        return data;
    } catch (error) {
        console.error(error);
    }
}

export const httpDeleteQuestion = async (questionId) => {
    try {
        const { data } = await axios.delete(`${API_URL}/question/${questionId}`);
        return data;
    } catch (error) {
        console.error(error);
    }
}

export const httpClearQuestions = async (documentId) => {
    try {
        const { data } = await axios.delete(`${API_URL}/question/clear/${documentId}`);
        return data;
    } catch (error) {
        console.error(error);
    }
}