import React from 'react';

import {
    Typography,
    Box
} from '@mui/material';

const Clauses = ({ summary, qAndA }) => {
  return (
    <Box sx={{
        marginTop: '1rem',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: qAndA ? 'center' : 'flex-start'
    }}>
        {qAndA && <Typography variant="h4" mb={2}>Summary:</Typography>}
        {
            summary.map((clause, i) => (
                <Typography key={i} variant='h6'>&bull; {clause}</Typography>
            ))
        }
    </Box>
  )
}

export default Clauses
